:root {
  --font-family: "FuturaNew", sans-serif;
  --font-family-second: "SchoolBook", sans-serif;
  --content-width: 1750px;
  --content-width-wide: 1640px;
  --content-width-narrow: 1350px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width--wide: calc(var(--content-width--wide) + (var(--container-offset) * 2));
  --container-width--narrow: calc(var(--content-width--narrow) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #080606;
  --main-color-dark: #59443f;
  --main-color-light: #ffc59e;
  --grey-color: #c3b9b5;
  --grey-light-color: #ede9e8;
  --bg-light: #ddd4f3;
  --bg-color: #fbf8f8;
  --main-fz: 20px;
  --phone-fz: 16px;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Regular.woff2") format("woff2"), url("../FuturaNew-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "SchoolBook";
  src: url("../fonts/SchoolBook-Regular.woff2") format("woff2"), url("../SchoolBook-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FuturaNew";
  src: url("../fonts/FuturaNew-Bold.woff2") format("woff2"), url("../FuturaNew-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

a {
  color: inherit;
  text-decoration-color: transparent;
  transition: color 0.3s ease 0s;
}

address {
  font-style: normal;
}

html,
body {
  height: 100%;
}

body {
  color: var(--main-color-dark);
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.lock {
  overflow: hidden;
  touch-action: none;
}

.page {
  font-family: var(--font-family, sans-serif);
  font-size: var(--main-fz);
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}
@media only screen and (max-width: 559px) {
  .page {
    font-size: var(--phone-fz);
  }
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.container--content-wide {
  max-width: var(--content-width-wide);
}

.container--content-narrow {
  max-width: var(--content-width-narrow);
}

.logo {
  flex-shrink: 0;
}
.logo__img {
  object-fit: contain;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox-hidden,
.radio-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--main-color-dark);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}
@media only screen and (max-width: 767px) {
  .burger {
    display: block;
  }
}

.btn {
  border-radius: 30px;
  display: inline-block;
  color: var(--light-color);
  font-weight: 700;
  padding: 20px 80px;
  text-transform: uppercase;
}
@media only screen and (max-width: 559px) {
  .btn {
    padding: 15px 50px;
  }
}
.btn--brown {
  background-color: var(--main-color-light);
  box-shadow: 0px 4px 20px var(--main-color-light);
  transition: background-color 0.3s ease 0s, box-shadow 0.3s ease 0s;
}
@media (any-hover: hover) {
  .btn--brown:hover {
    background-color: var(--main-color-dark);
    box-shadow: 0px 4px 20px var(--main-color-dark);
  }
}
.btn--grey {
  background-color: var(--grey-light-color);
  box-shadow: 0px 4px 20px var(--grey-light-color);
}
.btn--up {
  background-color: var(--light-color);
  border-radius: 50px;
  color: var(--main-color);
  font-weight: 400;
  padding: 11px 46px;
}

.btn-prev,
.btn-next {
  background-color: var(--main-color-light);
  border-radius: 50%;
  position: absolute;
  width: 40px;
  height: 40px;
  top: 50%;
  transform: translateY(-50%);
  transition: background-color 0.3s ease 0s, box-shadow 0.3s ease 0s;
}
@media (any-hover: hover) {
  .btn-prev:hover,
.btn-next:hover {
    background-color: var(--main-color-dark);
    box-shadow: 0px 2px 10px rgba(155, 82, 50, 0.5);
  }
}
.btn-prev::before,
.btn-next::before {
  content: "";
  background-image: url(../images/icons/icon-arrow.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 14px;
  display: block;
}

.btn-prev::before {
  transform: rotate(180deg);
}

